import React from "react";
// import SubTasksList from "./SubTasksList.js";
import CN from "classnames";

/* const TasksList = (task, index, deleteTask, checkTask) => { */
const TasksList = (props) => {
  const label = CN({
    "w-full pl-3 cursor-pointer overflow-ellipsis overflow-hidden": true,
    "line-through text-gray-500": !props.state,
  });
  const iconcheck = CN({
    "circle-int": props.state,
    "circle-int-checked": !props.state,
  });
  const labelStyle= ()=>
  (props.label ? ({backgroundColor: props.label.label.color}) : ({backgroundColor: ""
    }))
    console.log(labelStyle());
   
  console.log("render list");
  return (
    
    <div className="flex justify-between relative ">
      <div className="flex items-center justify-center">
      <div 
      style={labelStyle()}
      className="h-8 w-4 rounded  absolute">
      </div>
      </div>
      <div className="w-full relative flex items-center bg-gray-800 hover:bg-gray-700  border-gray-700  border-b-2  pl-3 shadow-lg py-3 px-5 ">
        <div onClick={props.checkTask} className="circle-ext cursor-pointer">
          <div className={iconcheck}></div>
        </div>
        <div onClick={props.checkTask} className={label}>
          {props.value}
        </div>
      </div>
      <div
        onClick={props.deleteTask}
        className="absolute cursor-pointer right-0 text-sm bg-gray-700 hover:bg-indigo-600 w-4 h-4 flex items-center justify-center"
      >
        x
      </div>
    </div>
  
  );
};

export default TasksList;
