import React, { useState } from "react";
import unselected from "../icon/unselected.svg";
import CN from "classnames";
// import CN from "classnames";

const SelectLabel = (props) => {
  const [inputlabel, setInputLabel] = useState("");

  const makeLabelStyle= CN ({
    "cursor-pointer bg-gray-800 px-2 py-1 rounded hover:bg-gray-400 text-sm ": true, 
    "cursor-not-allowed": (inputlabel === "" || props.labelColor === false)
  });
  const noneLabel= CN({
    "rounded justify-self-center  h-6 w-6 border border-white shadow-lg mb-2 cursor-pointer":true,
    "animate-bounce": !props.labelColor
  });

  const handleChange = (event) => {
    setInputLabel(event.target.value);
  };

  function addLabel() {
    if (inputlabel === "" || props.labelColor === false) {
      return null;
    }
    setInputLabel("");
    props.setLabelArray({ text: inputlabel, color: props.labelColor });
  }

  const bgcolor = [
    "#FCA5A5",
    "#F87171",
    "#EF4444",
    "#DC2626",
    "#B91C1C",
    "#991B1B",
    "#7F1D1D",
    "#FCD34D",
    "#FBBF24",
    "#F59E0B",
    "#D97706",
    "#B45309",
    "#92400E",
    "#78350F",
    "#6EE7B7",
    "#34D399",
    "#10B981",
    "#059669",
    "#047857",
    "#065F46",
    "#064E3B",
    "#93C5FD",
    "#60A5FA",
    "#3B82F6",
    "#2563EB",
    "#1D4ED8",
    "#1E40AF",
    "#1E3A8A",
    "#C4B5FD",
    "#A78BFA",
    "#8B5CF6",
    "#7C3AED",
    "#6D28D9",
    "#5B21B6",
    "#4C1D95",
    "#F9A8D4",
    "#F472B6",
    "#EC4899",
    "#DB2777",
    "#BE185D",
    "#9D174D",
    "#831843",
  ];

  let colors = [];
 

  bgcolor.forEach((element, index, currentValue) => {
    const selectedLabel= CN({
      "rounded justify-self-center  h-6 w-6 border border-white shadow-lg mb-2 cursor-pointer":true,
      "animate-bounce": element === props.labelColor
    });
    const mystyle = {
      backgroundColor: element,
    };
    colors.push(
      <div
        onClick={() => props.setLabelColor(element)}
        className={selectedLabel}
        key={index}
        style={mystyle}
      ></div>
    );
  });

  const labelcolor = {
    backgroundColor: props.labelColor,
  };
  return (
    <div className="sm:inline-block relative justify-center bg-gray-700 mb-5 rounded-b border-t border-gray-800 p-3">
      <div
        onClick={props.setShowLabel}
        className="absolute cursor-pointer right-0 top-0 text-sm bg-gray-700 hover:bg-indigo-600 w-4 h-4 flex items-center justify-center"
      >
        x
      </div>
      <div className="text-xs uppercase inline-block mb-2 ">Select color:</div>
      
      <div className="grid-color">
        {" "}
        {colors}
        {/* <div className="mb-5 bg-gray-700 p-3 grid grid-flow-col grid-cols-6 grid-rows-3 gap-3 md:gap-4"> {colors}</div> */}
      </div>
      {/* None selected */}
      <div className="flex justify-center">
      <div
        onClick={() => props.setLabelColor(false)}
        /* className="rounded justify-self-center  h-6 w-6 border border-white shadow-lg mb-2 cursor-pointer" */
        className={noneLabel}
      >
        <img src={unselected} alt="unselected" />
      </div>
      </div>
      <input
        type="text"
        value={inputlabel}
        onChange={handleChange}
        placeholder="Add label name!"
        className="rounded placeholder-gray-50  shadow-lg break-words appearance-none focus:outline-none  w-full py-1 px-3 text-grey-darker bg-gray-800 mb-2 "
        style={labelcolor}
      ></input>
      <div className="flex justify-center">
        <button
          onClick={addLabel}
          /* className="cursor-pointer bg-gray-800 px-2 py-1 rounded hover:bg-gray-400 text-sm " */
          className={makeLabelStyle}
        >
          {" "}
          make label{" "}
        </button>
      </div>
    </div>
  );
};

export default SelectLabel;
