import React, {
  useState,
  useEffect,
  createContext,
  useCallback,
  useMemo,
} from "react";
// import { where, filter } from "lodash";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import nextId from "react-id-generator";
import CN from "classnames";
 import DraggableList from "./components/DraggableList.js";
import InputLabel from "./components/InputLabel.js";
import SelectLabel from "./components/SelectLabel.js";
import TasksList from "./components/TasksList.js";
import Footer from "./components/Footer";
import LabelsList from "./components/LabelsList.js";
import iconSun from "./icon/icon-sun.svg";
import iconMoon from "./icon/icon-moon.svg";

function ToDo(props) {
  const [inputlabel, setInputLabel] = useState("");
  const [showLabel, setShowLabel] = useState(false);
  const [labelArray, setLabelArray] = useState([
    { index: "id1", label: { text: "house", color: "#F59E0B" } },
    { index: "id2", label: { text: "work", color: "#2563EB" } },
    { index: "id3", label: { text: "hobbys", color: "#1E40AF" } }
  ]);
  const [choosenlabel, setChoosenLabel] = useState(false);
  const [labelColor, setLabelColor] = useState(false);
  const [filter, setFilter] = useState("All");
  const [tasks, setTasks] = useState([]);
  const _ = require("lodash");

  const handleChange = (event) => {
    setInputLabel(event.target.value);
  };

  //cancella l'elemento di posizione Index dall'array di Task
  const deleteTask = useCallback(
    (i) => {
      console.log("Ho chiamato deleteTask");
      let newArray = tasks.slice();
      newArray = _.filter(newArray, function (currentObject) {
        return currentObject.index !== i;
      });
      setTasks(newArray);
    },
    [tasks, _]
  );

  const checkTask = useCallback(
    (i) => {
      let newArray = tasks.slice();
      let updateindex = _.findIndex(newArray, { index: i });
      let objCopy = newArray[updateindex];
      objCopy.state = !objCopy.state;
      newArray.splice(updateindex, 1, objCopy);
      setTasks(newArray);
    },
    [tasks, _]
  );

  function updateFilter(value) {
    setFilter(value);
  }

  function filterArray() {
    let arrayCopy = tasks.slice();
    switch (filter) {
      case "All":
        return arrayCopy;
      case "Active":
        return arrayCopy.filter((el) => el.state === true);
      case "Completed":
        return arrayCopy.filter((el) => el.state === false);
      default:
        return null;
    }
  }

  function clearCompleted() {
    let arrayCopy = tasks.slice();
    arrayCopy = arrayCopy.filter((el) => el.state === true);
    setTasks(arrayCopy);
  }

  function addLabel(label) {
    setLabelArray((old) => [...old, { index: nextId(), label }]);
  }

  function addTask(event) {
    if (event === "" || event === undefined) {
      return null;
    } else {
      console.log(choosenlabel);
      setTasks((old) => [
        ...old,
        { index: nextId(), state: true, value: event, sublist: [], label:choosenlabel},
      ]);
      setInputLabel("");
      setChoosenLabel(false);
    }
  }

  //crea event listener, in attesa della pressione del tasto "enter"
  useEffect(() => {
    const listener = (event) => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        console.log("Enter key was pressed. Run your function.");
        if (event.target.id === "input-label") {
          // chiamo la funzione addtask passandogli il value dell'evento
          // triggerato dal onChange dell'input, solo se il target
          // dell'event è input-label
          
          addTask(event.target.value);
        } else {
          return null;
        }
      }
    };
    document.addEventListener("keydown", listener);
    return () => {
      document.removeEventListener("keydown", listener);
    };
  }, [choosenlabel]);

  let taskArray = filterArray();
  
  return (
    <div className=" w-11/12 lg:w-2/3 absolute container mx-auto inset-x-0  toDoBox text-white text-xl ">
      <div className="flex justify-between mb-8">
        <div className="text-4xl tracking-widest "> TODO</div>
        <div
          className="self-center cursor-pointer"
          onClick={props.toggleNightMode}
        >
          <img src={props.nightMode ? iconSun : iconMoon} alt="iconsun" />
        </div>
      </div>

      {/*########################### Input Label ################## */}
      <InputLabel
        inputlabel={inputlabel}
        handleChange={handleChange}
        choosenlabel={choosenlabel}
        addTask={(t)=>addTask(t)}
      >
        {" "}
      </InputLabel>
      <LabelsList
        labelArray={labelArray}
        setChoosenLabel={(obj) => setChoosenLabel(obj)}
      ></LabelsList>
      {/*########################### add label button ################## */}
      {!showLabel ? (
        <div
          onClick={() => setShowLabel(!showLabel)}
          className="w-min whitespace-nowrap ml-2 mb-5 cursor-pointer text-xs py-1 px-1 bg-gray-700 border-b border-black rounded-b-lg border-t border-gray-800  uppercase"
        >
          + Make label
        </div>
      ) : null}

      {/*########################### Select Label component ################## */}
      <CSSTransition
        unmountOnExit
        in={showLabel}
        timeout={300}
        classNames="my-label"
        onExited={() => setShowLabel(false)}
      >
        <SelectLabel
          labelColor={labelColor}
          setLabelArray={(obj) => addLabel(obj)}
          setLabelColor={(color) => setLabelColor(color)}
          setShowLabel={() => setShowLabel(!showLabel)}
          display={showLabel}
        ></SelectLabel>
      </CSSTransition>

      {/* Entry point x array di tasks + CSS TRANSITION GROUP */}

      <TransitionGroup>
        {useMemo(
          () =>
            taskArray.map((e) => (
              <CSSTransition
                key={e.index}
                timeout={350}
                classNames="transition"
              >
                <TasksList
                  key={e.index}
                  {...e}
                  deleteTask={() => deleteTask(e.index)}
                  checkTask={() => checkTask(e.index)}
                />
              </CSSTransition>
            )),
          [taskArray, deleteTask, checkTask]
        )}
      </TransitionGroup>

      {/* ACCEPT ONLY ARRAYS, FOR THE MOMENT */}
     {/*   {        <DraggableList items={taskArray}/>       
       } */}
       

      {/*###################### Recap footer ###################### */}

      <Footer
        arraylength={tasks.length}
        clearCompleted={() => clearCompleted()}
        updateFilter={(e) => updateFilter(e)}
        filter={filter}
      ></Footer>

      {/* Contains under menù (shows on mobile only) */}

      <p className="text-center mt-6 text-gray-600">
        {" "}
        Drag and drop to reorder list
      </p>
    </div>
  );
}

export default ToDo;
