import React from "react"; 
import CN from "classnames";

// function for returning ClassName on Active, All , Completed Button
function giveClassName(item) {
    let classN = "";
    classN = CN({
      "cursor-pointer mx-1": true,
      "text-gray-200 hover:text-gray-100 animate-bounce": item.filter === item.info,
      "text-gray-600 hover:text-gray-100": item.filter !== item.info,
    });
    return classN;
  }

const Footer = (props) => 

{
return(
    <>
    <div className="flex justify-between shadow appearance-none bg-gray-800  py-3 px-5  shadow-lg rounded-bl-lg rounded-br-lg ">
    <div className="w-full flex">
      <div className=" w-full  flex justify-between ">
        <div className="md:w-1/3  text-gray-600 hover:text-gray-500 cursor-pointer">
          {props.arraylength} items left
        </div>
        <div className="md:w-1/3 flex  hidden md:flex justify-between">
          <div
            onClick={() => props.updateFilter("All")}
            className={giveClassName({info:"All", filter:props.filter})}
          >
            All
          </div>
          <div
            onClick={() => props.updateFilter("Active")}
            className={giveClassName({info:"Active", filter:props.filter})}
          >
            Active
          </div>
          <div
            onClick={() => props.updateFilter("Completed")}
            className={giveClassName({info:"Completed", filter:props.filter})}
          >
            Completed
          </div>
        </div>
        <div
          onClick={() => props.clearCompleted()}
          className=" md:w-1/3  text-gray-600 hover:text-gray-500 cursor-pointer text-right"
        >
          Clear Completed
        </div>
      </div>
    </div>
  </div>

  {/* under menù (shows on mobile only) */}

  <div className="block md:hidden w-full flex bg-gray-800  shadow-lg mt-4 rounded-lg">
        <div className="shadow appearance-none w-full py-3 px-10 text-xl text-grey-darker bg-gray-800 rounded-lg">
          <div className="flex justify-around  w-full">
            <div
              onClick={() => props.updateFilter("All")}
              className={giveClassName({info:"All", filter:props.filter})}
            >
              All
            </div>
            <div
              onClick={() => props.updateFilter("Active")}
              className={giveClassName({info:"Active", filter:props.filter})}
            >
              Active
            </div>
            <div
              onClick={() => props.updateFilter("Completed")}
              className={giveClassName({info:"Completed", filter:props.filter})}
            >
              Completed
            </div>
          </div>
        </div>
      </div>
  </>
)

}

export default Footer