
import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { Tooltip } from "@material-ui/core";
import arrow from "../icon/arrow.svg";


const InputLabel = (props)=>  {

    //Tooltip custom
  const LightTooltip = withStyles((theme) => ({
    tooltip: {
      backgroundColor: theme.palette.common.white,
      color: "rgba(0, 0, 0, 0.67)",
      boxShadow: theme.shadows[1],
      fontSize: 14,
    },
  }))(Tooltip);

const borderstyle= props.choosenlabel?  "5px solid" +  props.choosenlabel.label.color: "0px";
  const mystyle={
    border:  borderstyle
  }

    return(
        <div className="relative flex justify-between ">
        <LightTooltip
          title="write your task and press Enter"
          placement="top-start"
        >
          <div className="hidden sm:flex absolute info-tooltip cursor-pointer border border-gray-200 text-sm bg-gray-800 hover:bg-indigo-600 w-5 h-5 rounded-full items-center justify-center">
            ?
          </div>
        </LightTooltip>
        <div className=""> </div>
        <div style={mystyle} className="w-full flex items-center bg-gray-800  pl-3 shadow-lg rounded-lg">
          <div className="circle-ext">
            <div className="circle-int"></div>
          </div>

          <input
            id="input-label"
            type="text"
            placeholder="Add your task!"
            value={props.inputlabel}
            onChange={props.handleChange}
            className="rounded-r break-words appearance-none focus:outline-none  w-full py-3 px-3 text-grey-darker bg-gray-800  "
          ></input>
          <div 
          onClick={()=>props.addTask(props.inputlabel)}
          className="bg-gray-600 hover:bg-gray-400 transition ease-out duration-300 tr rounded-full mr-2 p-1 cursor-pointer ">
            <img src={arrow} className="w-4 h-4 transform rotate-180  fill-white"></img>
          </div>
        </div>
      </div>
    )
}

export default InputLabel