import React , {useState} from 'react';
import CN from "classnames";

import ToDo from "./ToDo.js";

function App() {
  const [nightMode, setNightMode] = useState(true);

  function switchNight() { 
    setNightMode(!nightMode)
  }
  const nightM = CN ({
    "text-lg font-jose min-h-screen h-full":true,
    "bg-dark": nightMode,
    "bg-light": !nightMode,
  });
  const imgsrc= CN ({
    "./img/bg-desktop-dark.jpg": nightMode,
    "./img/bg-desktop-light.jpg": !nightMode,
  })
 
  return (   
    <div className={nightM} >
      <img src={imgsrc} className="w-full object-cover h-64" alt="bg-dark" ></img>
       <ToDo toggleNightMode={()=> switchNight()} nightMode={nightMode}></ToDo> 
     
    </div>
  );
}

export default App;
