import React from "react";

const LabelsList = (props) => {
  return (
    <div className="text-sm flex flex-wrap  ">
       <div
        onClick={()=>props.setChoosenLabel(false)}
          className="m-1 cursor-pointer rounded px-1 mr-2 border border-white"
        >
          {" "}
         none
        </div>
      {props.labelArray.map((element) => (
        <div
        onClick={()=>props.setChoosenLabel(element)}
          className="m-1 cursor-pointer rounded px-1 mr-2"
          key={element.index}
          style={{ backgroundColor: element.label.color }}
        >
          {" "}
          {element.label.text}
        </div>
      ))}
    </div>
  );
};

export default LabelsList;
